export function password(password) {
  let r = /^[0-9A-z]{6,12}/;
  return r.test(password);
}

export function mobileNum(mobile) {
  return mobile.length == 11;
}

export function withdrawalPwd(pwd) {
  let r = /[0-9]{4}/;
  return r.test(pwd);
}

export function name(name) {
  let r = /^[0-9A-z\_\-]{4,10}/;
  return r.test(name);
}


