import React, {Component, PropTypes} from 'react';
import Slider from 'react-slick';
import {resourceUrl} from '../utils/url';

class HomeSlider extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
    };

    return (
      <div className="home-slider">
        <Slider {...settings}>
          <div><img src={resourceUrl('/images/slider/slider1.jpg')} /></div>
          <div><img src={resourceUrl('/images/slider/slider2.jpg')} /></div>
        </Slider>
      </div>
    );
  }
}

export default HomeSlider;


